import React, { useContext } from 'react'
import styles from "./NarratorText.module.css"
import Inlines from '../Inlines/Inlines'
import SourceName from '../SourceName/SourceName'
import AppContext from '../../contextAPI/AppContext'

const NarratorText = (props) => {
    const receivedAppContext = useContext(AppContext)

    if (receivedAppContext.language === 'HBR') {
        return (
            <div className={styles.hebrew}>
                <SourceName 
                    sourceName={props.sourceData.source}
                    data={props.sourceData}
                />
                <div>
                {props.inlines.map(inline => {
                    return <Inlines 
                    type={inline.type}
                    leaves={inline.nodes}/>
                })}
                </div>
            </div>
    )} else {
        return (
            <div>
                <SourceName 
                    sourceName={props.sourceData.source}
                    data={props.sourceData}
                />
                <div>
                {props.inlines.map(inline => {
                    return <Inlines 
                    type={inline.type}
                    leaves={inline.nodes}/>
                })}
                </div>
            </div>
    )}
}

export default NarratorText;