import React, { useContext } from 'react'
import styles from './SourceName.module.css'
import AppContext from '../../contextAPI/AppContext'

const SourceName = (props) => {
    const receivedAppContext = useContext(AppContext)
    return (
    <p className={styles.sourceName}
    onClick={() => receivedAppContext.updateSourceDialog({open: !receivedAppContext.sourceDialog.open, content: props.data})}
    >{props.sourceName}</p>
)}

export default SourceName;