import React from 'react'
import styles from './Paragraph.module.css'

const Paragraph = (props) => {
    return (
        <p>
            {props.content[0].leaves.map(leaf => { 
                let classNames = []
                if (leaf.marks) {
                    if (leaf.marks.find(mark => mark.type === 'bold')) {
                        classNames.push('bold')
                    }
                    if (leaf.marks.find(mark => mark.type === 'italic')) {
                        classNames.push('italic')
                    }
                    if (leaf.marks.find(mark => mark.type === 'smallcaps')) {
                        classNames.push('smallcaps')
                    }
                }
                    return <span className={(classNames.length > 0) ? classNames.map(mark => styles[mark]).join(' ') : ''}>{leaf.text}</span>})
            }
        </p>
    )
}

export default Paragraph