import React from 'react'
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import { Doughnut } from 'react-chartjs-2'

import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles({
  main: {
    display: 'flex',
    justifyContent: 'center',
    flexWrap: 'wrap',
    textAlign: 'center',
    marginBottom: '8px',
    maxHeight: 'fit-content',
  },
  card: {
    maxHeight: '150px',
    margin: '5px',
  },
  leftCard: {
    marginRight: '8px'
  },
  top5: {
    flex: 1,
    textAlign: 'center',
    fontSize: '75%',
    padding: 0
  },
  cardTitle: {
    margin: '0',
    textDecoration: 'underline',
    fontWeight: 'bold',
    fontSize: '80%',
    padding: '2px'
  },
  top5List: {
    padding: '1px',
    margin: 1,
  },
  cardContent: {
    margin: 0,
    padding: 0,
    fontSize: '11px',
    lineHeight: '13px'
  },
  backgroundColor: {
    backgroundColor: 'rgba(58, 76, 125, 0.07)'
  },
  red: {
    color: '#d60000'
  },
  green: {
    color: '#006400'
  },
  blue: {
    color: 'darkblue'
  },
  topSources: {
    flex: '1 100px'
  },
  topWords: {
    flex: '1 100px'
  },
  colorComparison: {
    flex: '1 1 300px',
  }
})

const getTopSources = (obj) => {
    const sourceNames = Object.keys(obj);
    let sourcesArray = sourceNames.map(sourceName => {
        return {
            key: sourceName,
            val: obj[sourceName].words,
            color: obj[sourceName].color
        }
    })

    sourcesArray = sourcesArray.sort(function (a, b) {
        if (a.val > b.val) {
            return -1
        } else if (a.val < b.val) {
            return 1
        } else if (a.val === b.val) {
            return 0
        }
    });
    const top5Sources = []
    let count = 0;
    sourcesArray.forEach(source => {
        if (source.key !== "The Narrator" && count < 5) {
            top5Sources.push({
              name: source.key,
              words: source.val, 
              color: source.color
            })
            count += 1;
        } else return
    })
    return top5Sources
}

const createColorData = (obj) => {
  const labels = Object.keys(obj);
  labels.pop()
  const values = Object.values(obj)
  values.pop()
  return {
    labels: ['Narrator', 'God', 'Main Character(s)', 'Everyone Else'],
    datasets: [{
      data: [
      Math.round((values[0] / obj.total) * 100),
      Math.round((values[3] / obj.total) * 100),
      Math.round((values[2] / obj.total) * 100),
      Math.round((values[1] / obj.total) * 100)
      ],
      backgroundColor: [
        'grey',
        '#ffadad',
        '#b9f8b9',
        'rgb(142, 227, 255)',
        ],
      hoverBackgroundColor: [
        'black',
        '#d60000',
        '#006400',
        'darkblue',
        ]
    }]
  }
}

const chartOptions = {
  responsive: true,
  maintainAspectRatio: true,
  layout: {
    padding: {
      left: 10,
      right: 10,
      top: 0,
      bottom: 50
    }
  },
  legend: {
    position: 'right',
    labels: {
      fontSize: 9,
      boxWidth: 40
    }
    // display: false
  },
}

const SegmentData = (props) => {
    const classes = useStyles();
    return (
      <div className={classes.main}>
        <Card className={`${classes.backgroundColor} ${classes.card} ${classes.colorComparison}`}>
          <CardHeader title="Color Comparison" disableTypography="true" className={classes.cardTitle}/>
          <CardContent className={`${classes.cardContent} ${classes.chart}`}>
            <Doughnut data={createColorData(props.colors)} options={chartOptions} />
          </CardContent>
        </Card>
        <Card className={`${classes.top5} ${classes.leftCard} ${classes.backgroundColor} ${classes.card}  ${classes.topSources}`}>
          <CardHeader title="Top Sources (words)" disableTypography="true" className={classes.cardTitle}/>
          <CardContent className={classes.cardContent}>
          {getTopSources(props.sources).map(source => <p className={`${classes.top5List} ${classes[source.color]} ${classes.topWords}`} >{source.name} ({source.words})</p>)}
          </CardContent>
        </Card>
        <Card className={`${classes.top5} ${classes.backgroundColor} ${classes.card} ${classes.topWords}`}>
            <CardHeader title="Top Repeated Words" disableTypography="true" className={classes.cardTitle}/>
            <CardContent className={classes.cardContent}>
            <p className={classes.top5List}>{props.repeatedWords[0]}</p>
            <p className={classes.top5List}>{props.repeatedWords[2]}</p>
            <p className={classes.top5List}>{props.repeatedWords[1]}</p>
            <p className={classes.top5List}>{props.repeatedWords[3]}</p>
            <p className={classes.top5List}>{props.repeatedWords[4]}</p>
          </CardContent>
        </Card>
      </div>
)}

export default SegmentData;