import React, { useContext, useState } from 'react'
import styles from './Leaf.module.css'
import AppContext from '../../contextAPI/AppContext'
import Popper from '@material-ui/core/Popper';
import Typography from '@material-ui/core/Typography';
import Fade from '@material-ui/core/Fade';
import Paper from '@material-ui/core/Paper';
import ClickAwayListener from "@material-ui/core/ClickAwayListener";

import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(theme => ({
    typography: {
      padding: theme.spacing(2),
      fontSize: 'small',
      color: 'white'
    },
    paper: {
        background: '#3A4C7D',
        width: 'fit-content',
        maxWidth: '60vw'
    }
  }));

const Leaf = (props) => {
    // This controls the visibility of chapter and verse references
    const receivedAppContext = useContext(AppContext);
    const [anchorEl, setAnchorEl] = useState(null);
    const classes = useStyles();

    // Create CSS styles from marks
    const chapter = (props.marks) ? props.marks.find(mark => mark.type === 'chapter') : undefined
    const verse = (props.marks) ? props.marks.find(mark => mark.type === 'verse') : undefined
    const footnote = (props.marks) ? props.marks.find(mark => mark.type === 'footnote') : undefined
    const bibleRef = (verse || chapter) ? props.marks.find(mark => mark.type === 'reference') : undefined
    const classNames = (verse !== undefined || chapter !== undefined) ? [(chapter !== undefined)?styles.chapter:null, (verse)?styles.verse:null, styles.leaf].join(' ') : styles.leaf
    
    function handleClick(event) {
        setAnchorEl(anchorEl ? null : event.currentTarget);
      }
    
    function handleClickAway() {
        setAnchorEl(null)
    }

    const open = Boolean(anchorEl);
    // If it's just a chapter/verse reference then return that leaf with a reference
    if (verse || chapter) {
        return <span id={`${bibleRef.data.book}-${bibleRef.data.chapter}-${bibleRef.data.verse}`} className={(classNames !== null) ? classNames : ''} style={{display: (receivedAppContext.displayRef) ? '' : 'none'}}>{props.text}{(chapter)?'':'\u00A0'}</span>
    }
    // If not a verse, don't create a reference
     else {
        return (<span>
            {footnote ? (
                <span className={(classNames !== null) ? classNames : ''} onClick={handleClick} onMouseOver={handleClick} onMouseLeave={handleClick}>
                    {props.text}
                    <Popper id="footnote" open={open} anchorEl={anchorEl} transition placement="top">
                            {({ TransitionProps }) => (
                                <ClickAwayListener onClickAway={handleClickAway}>
                                    <Fade {...TransitionProps} timeout={350}>
                                        <Paper className={classes.paper}>
                                        <Typography className={classes.typography}>{footnote.data.footnote}</Typography>
                                        </Paper>
                                    </Fade>
                                </ClickAwayListener>
                            )}
                    </Popper>
                </span>
            ) : <span className={(classNames !== null) ? classNames : ''}>{props.text}</span>}
        </span>
        )
     }
     
    }

export default Leaf;