import React, { useEffect, useContext } from 'react'
import AppContext from '../contextAPI/AppContext'
import scrollToElement from 'scroll-to-element'
import SpeechBubble from '../components/SpeechBubble/SpeechBubble'
import Introduction from '../components/Introduction/Introduction'
import Narrator from '../components/NarratorText/NarratorText'
import SegmentData from '../components/SegmentData/SegmentData'
import ReadTogether from '../images/SVB-Read-together.png'
import DiscussTogether from '../images/SVB-Discuss-together.png'

import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(theme => ({
  typography: {
    padding: theme.spacing(2),
    fontSize: 'small',
    color: 'white'
  },
  paper: {
      background: '#3A4C7D',
      width: '75vw'
  },
  segmentDataContainer: {
    display: 'flex',
    justifyContent: 'center',
    margin: '10px 0',
    padding: '10px 0',
  },
  header: {
    display: 'flex',
    justifyContent: 'center',
    flexWrap: 'wrap',
    paddingTop: '5px'
  },
  title: {
    fontSize: '150%',
    fontWeight: ' bold',
    textDecoration: 'underline',
    width: '100%',
    margin: 0,
    textAlign: 'center'
  },
  reference: {
    margin: 0,
    fontSize: '60%',
    color: 'grey'
  },
  discuss: {
    display: 'block',
    textAlign: 'center',
    margin: '10px 0',
    padding: '10px 0',
    borderTop: '0.25px lightgrey solid',
  },
  footer: {
    textAlign: 'center',
    fontSize: '60%',
    margin: '10px 0 30px 0',
    '& p': {
      margin: 0
    }
  },
  images: {
    width: '200px',
    margin: '10px auto',
  },
  readImage: {
    marginBottom: '20px',
    borderBottom: '0.25px lightgrey solid',
  },
  content: {
    margin: '0 0 10px 0',
    padding: '0 0 10px 0'
  },
  questions: {
    textAlign: 'left',
    margin: 0,
    '& p': {
      margin: '10px 0',
    }
  },
  imgCaption: {
    color: 'grey',
    fontSize: '70%',
    margin: '-20px 0 0 0',
    textAlign: 'center'
  }
}))


export default ({pageContext: {segment}}) => {
  const classes = useStyles();
  const receivedAppContext = useContext(AppContext);
  
  useEffect(() => {
    if (typeof window !== undefined) {
      
      if (receivedAppContext.searchID !== '') {
        const id = `#${receivedAppContext.searchID}`
        scrollToElement(id, {
            offset: -65,
            ease: 'inOutCube',
            duration: 1500
        });
        receivedAppContext.updateSearchID('')
      }
    }
  }, [])

  const currentYear = new Date().getFullYear()

  return (
    <div>
      <div className={classes.header}>
        <p className={classes.title}>Title</p>
        <p className={classes.reference}>Reference</p>
      </div>
      {/* segments start with an array of block nodes (so they have a length), but intros start with an object "document"  */}
      {(segment.content) ? (
        <div>
          <div className={classes.segmentDataContainer}>
            <SegmentData 
              colors={segment.colors}
              sources={segment.sources}
              repeatedWords={segment.repeatedWords}
            />
          </div>
          <div className={`${classes.images} ${classes.readImage}`}>
            <img src={ReadTogether} alt="Read Together" />
            <p className={classes.imgCaption}>Each person reads a color aloud</p>
          </div>
          <div className={classes.content}>
            {segment.content.map(block => {
              if (block.data.color === 'black') {
                return (<Narrator 
                  sourceData={block.data}
                  inlines={block.nodes} />
                  )
              } else {
                return (<SpeechBubble 
                  sourceData={block.data}
                  inlines={block.nodes} />
                  )
              }
            })}
          </div>
          <div className={classes.discuss}>
            <div className={classes.images}>
              <img src={DiscussTogether} alt="Discuss Together" />
              <p className={classes.imgCaption}>Discuss the questions with others</p>
            </div>
            <p className={classes.title}>Title</p>
            <div className={classes.questions}>
              <p>Question #1: this is a really, really, really, really, really, really, really, really, really long question</p>
              <p>Question #2: this is a really, really, really, really, really, really, really, really, really long question</p>
              <p>Question #3: this is a really, really, really, really, really, really, really, really, really long question</p>
              <p>Question #4: this is a really, really, really, really, really, really, really, really, really long question</p>
            </div>
          </div>
          <div className={classes.footer}>
            <p>copyright info of version</p>
            <p>Â© {currentYear} SourceView LLC.</p>
          </div>
        </div>
    ) : (
        <Introduction content={segment.document.nodes} />
      )}
    </div>
  )}