import React, { useContext } from 'react'
import SourceName from '../SourceName/SourceName'
import styles from "./SpeechBubble.module.css"
import Inlines from '../Inlines/Inlines'
import AppContext from '../../contextAPI/AppContext'

const SpeechBubble = (props) => {
    const receivedAppContext = useContext(AppContext)

    if (receivedAppContext.language === 'HBR') {
        return (
            <div className={styles.hebrew}>
                <SourceName 
                    sourceName={props.sourceData.source}
                    data={props.sourceData}
                />
                <div className={[styles.speechbubblehebrew, styles[props.sourceData.color]].join(' ')}>
                {props.inlines.map(inline => {
                    return <Inlines 
                    type={inline.type}
                    leaves={inline.nodes}/>
                })}
                </div>
            </div>
        )
    } else {
        return (
            <div>
                <SourceName 
                    sourceName={props.sourceData.source}
                    data={props.sourceData}
                />
                <div className={[styles.speechbubble, styles[props.sourceData.color]].join(' ')}>
                {props.inlines.map(inline => {
                    return <Inlines 
                    type={inline.type}
                    leaves={inline.nodes}/>
                })}
                </div>
            </div>
        )
    }
    
    
}

export default SpeechBubble;