import React, { useContext } from 'react'
import styles from './Inlines.module.css'
import Leaf from '../Leaf/Leaf'
import InterlinearLeaf from '../Leaf/InterlinearLeaf'
import AppContext from '../../contextAPI/AppContext'

const Inlines = (props) => {
    const receivedAppContext = useContext(AppContext)
    if (props.type === 'prose') {
        return (
            <div className={styles.prose}>
                {props.leaves.map(leaf => {
                    if (receivedAppContext.interlinear) {
                        return <InterlinearLeaf 
                        marks={leaf.marks}
                        text={leaf.text}/>
                    } else {
                        return <Leaf 
                        marks={leaf.marks}
                        text={leaf.text}/>
                    }
                })}
            </div>
        )
    } else if (props.type === 'poetry') {
        return (
            <div className={styles.poetry}>
                {props.leaves.map(leaf => {
                    if (receivedAppContext.interlinear) {
                        return <InterlinearLeaf 
                        marks={leaf.marks}
                        text={leaf.text}/>
                    } else {
                        return <Leaf 
                        marks={leaf.marks}
                        text={leaf.text}/>
                    }
                })}
            </div>
        )
    } else {
        return null
    }
}

export default Inlines